import React, { useState, useEffect } from "react";
import axios from "axios";
import styled from "styled-components";

const AdminReqPlace = () => {
  const [region, setRegion] = useState("전국");
  const [data, setData] = useState([]);
  useEffect(() => {
    const getData = async () => {
      await axios({
        method: "get",
        url: `${process.env.REACT_APP_API_URL}/admin/place/getRequestList?region=${region}`,
      })
        .then((res) => {
          setData(res.data.data);
        })
        .catch((err) => {
          console.log(err);
        });
    };
    getData();
  }, [region]);
  const handleSelect = (e) => {
    setRegion(e.target.value);
  };
  return (
    <Container>
      <CenteredText>여행대로 유저 요청 장소 관리 페이지</CenteredText>
      <ContentContainer>
        <SelecetBoxArea>
          <ContentArea>
            <ContentLabel>지역</ContentLabel>
            <RegionSelect onChange={handleSelect} value={region}>
              <option value="전국">전국</option>
              <option value="경기">경기</option>
              <option value="서울">서울</option>
              <option value="경북">경북</option>
              <option value="인천">인천</option>
              <option value="부산">부산</option>
              <option value="제주">제주</option>
              <option value="강원">강원</option>
              <option value="충남">충남</option>
              <option value="경남">경남</option>
              <option value="전남">전남</option>
              <option value="충북">충북</option>
              <option value="대전">대전</option>
              <option value="전북">전북</option>
              <option value="대구">대구</option>
              <option value="광주">광주</option>
              <option value="울산">울산</option>
              <option value="세종">세종</option>
            </RegionSelect>
          </ContentArea>
        </SelecetBoxArea>
        <Title>요청사항</Title>
        <LabelArea>
          <RegionLabel>지역</RegionLabel>
          <TitleLabel>장소이름</TitleLabel>
          <NickNameLabel>닉네임</NickNameLabel>
          <DateLabel>요청시간</DateLabel>
        </LabelArea>
        {data.map((el, idx) => {
          return (
            <LabelArea key={idx}>
              <RegionLabel>{el.region}</RegionLabel>
              <TitleLabel>{el.title}</TitleLabel>
              <NickNameLabel>{el.user_name}</NickNameLabel>
              <DateLabel>{el.created_at}</DateLabel>
            </LabelArea>
          );
        })}
      </ContentContainer>
    </Container>
  );
};
const Container = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  position: relative;
`;
const CenteredText = styled.div`
  display: flex;
  width: 100%;
  font-size: 20px;
  font-weight: 800;
  justify-content: center;
  margin-bottom: 30px;
  padding-top: 30px;
`;
const ContentContainer = styled.div`
  display: flex;
  width: 900px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 20px;
`;
const SelecetBoxArea = styled.div`
  display: flex;
  width: 100%;
  justify-content: flex-start;
`;
const ContentArea = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 50px;
`;
const ContentLabel = styled.span`
  display: flex;
  font-size: 16px;
  font-weight: 700;
`;
const RegionSelect = styled.select`
  width: 120px;
  height: 24px;
`;
const Title = styled.div`
  display: flex;
  width: 100%;
  font-size: 16px;
  font-weight: 700;
  color: #000;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #999;
  padding: 10px 0;
`;
const LabelArea = styled.div`
  display: flex;
  width: 100%;
  flex-direction: row;
  font-size: 16px;
  font-weight: 700;
  color: #000;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
  border-bottom: 1px solid #999;
`;
const RegionLabel = styled.div`
  display: flex;
  width: 40px;
  align-items: center;
  justify-content: center;
`;
const TitleLabel = styled.div`
  display: flex;
  width: 450px;
  align-items: center;
  justify-content: center;
`;
const NickNameLabel = styled.div`
  display: flex;
  width: 200px;
  align-items: center;
  justify-content: center;
`;
const DateLabel = styled.div`
  display: flex;
  width: 180px;
  align-items: center;
  justify-content: center;
`;

export default AdminReqPlace;
